import axios from 'axios';
import { baseURL } from './utils';

/**
 * 充值
 * @param {*} param
 * @returns
 */
export const rechargeAdd = (param) => axios.post(`${baseURL}/recharge/add`, {}, { params: param }).catch(error => {
  console.error('充值失败', error.response);
});



/**
 * 充值记录
 * @param {*} param /api/app/{id}
 * @returns
 */
export const rechargeHistory = (param, page = 1) => axios.post(`${baseURL}/recharge/history/${page}/15`, {}, { params: param }).catch(error => {
  console.error('充值记录获取失败', error.response);
});



/**
 * 查询单一充值记录
 * @param {*} param
 * @returns
 */
export const rechargeOneHistory = (param) => axios.get(`${baseURL}/recharge/history/${param}`).catch(error => {
  console.error('充值记录获取失败', error.response);
});
