import axios from 'axios';
import { baseURL } from './utils';

export * from './recharge';
export * from './record';
export * from './member';
export * from './user';



/**
 * 店内全部美发项目
 * @param {*} param
 * @returns
 */
export const projectAll = param => axios
  .get(`${baseURL}/project/all`, param)
  .catch(error => {
    console.error('美发项目未获取到', error.response);
  });


/**
 * 登录
 * @param {*} param
 * @returns
 */
export const login = (param) => axios.post(`${baseURL}/login/signin`, param).catch(error => {
  console.error('登录失败', error.response);
});
