import axios from 'axios';
import { baseURL } from './utils';

// 收银/消费相关接口


/**
 * 付款方式列表
 * @param {*} param
 * @returns
 */
export const recordPaytypes = param => axios
  .get(`${baseURL}/record/paytypes`, param)
  .catch(error => {
    console.error('美发项目未获取到', error.response);
  });


/**
 * 新增收银记录
 * @param {*} param
 * @returns
 */
export const recordAdd = (param) => axios.post(`${baseURL}/record/add`, {}, { params: param }).catch(error => {
  console.error('收银失败', error.response);
});


/**
 * 收银记录
 * @param {*} param /api/app/{id}
 * @returns
 */
export const recordHistory = (param, page = 1) => axios.get(`${baseURL}/record/history/${page}/15`, { ...param }).catch(error => {
  console.error('收银记录获取失败', error.response);
});



/**
 * 查询单一收银记录
 * @param {*} param /history/{id}
 * @returns
 */
export const recordOneHistory = (param) => axios.get(`${baseURL}/record/history/${param}`).catch(error => {
  console.error('收银记录获取失败', error.response);
});
