import axios from 'axios';
import { baseURL } from './utils';

// 人员相关接口

/**
 * 添加员工
 * @param {*} param
 * @returns
 */
export const addUser = (param) => axios.post(`${baseURL}/user/add`, {}, { params: param }).catch(error => {
  console.error('添加员工失败', error.response);
});



/**
 * 修改密码
 * @param {*} param
 * @returns
 */
export const resetPwd = (param) => axios.post(`${baseURL}/user/resetPwd`, {}, { params: param }).catch(error => {
  console.error('密码修改失败', error.response);
});





/**
 * 店内所有员工
 * @param {*} deptId
 * @returns
 */
export const queryStaff = (deptId = 0) => axios
  .get(`${baseURL}/user/all/${deptId}`)
  .catch(error => {
    console.error('查询会员失败', error.response);
  });



