import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    staff: [], // 员工
    project: [], // 服务项目
    userInfo: {},
    paytypes: [] // 付款方式
  },
  getters: {
    // 用于计算相关属性
    staffSimple: state => {
      const sim = state.staff.map(item => item.userName);
      return sim;
    },
    staffSimpleMap: state => {
      const map = {};
      state.staff.forEach(item => { map[item.userName] = item.userId });
      return map;
    },
    paytypeMap: state => {
      const map = {};
      state.paytypes.forEach(item => { map[item.payType] = item.payTypeName });
      return map;
    },
    projectMap: state => {
      const map = {};
      state.project.forEach(item => { map[item.projectId] = item.projectName });
      return map;
    },
  },
  mutations: {
    setStaff(state, arr) {
      state.staff = arr;
    },
    setProject(state, arr) {
      state.project = arr;
    },
    setPaytypes(state, arr) {
      state.paytypes = arr;
    },
    setUserInfo(state, obj) {
      state.userInfo = obj;
    }
  },
  actions: {},
  modules: {}
});
