import Vue from 'vue';
import axios from 'axios';
import {
  Toast,
  Row,
  Col,
  Cell,
  CellGroup,
  NavBar,
  Tab,
  Tabs,
  Field,
  RadioGroup,
  Radio,
  Checkbox,
  CheckboxGroup,
  Divider,
  Image,
  NumberKeyboard,
  Loading,
  Search,
  IndexBar,
  IndexAnchor,
  Icon,
  Picker,
  Popup,
  Button
} from 'vant';
// import baseConf from '../../config/base';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.config.productionTip = false;

Vue.prototype.$store = store;
// axios.defaults.baseURL = baseConf.api;
Vue.prototype.$axios = axios;
Vue.prototype.$Toast = Toast;

Vue.use(Row)
  .use(Toast)
  .use(Col)
  .use(Cell)
  .use(CellGroup)
  .use(NavBar)
  .use(Tab)
  .use(Tabs)
  .use(Field)
  .use(RadioGroup)
  .use(Radio)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Divider)
  .use(Image)
  .use(NumberKeyboard)
  .use(Loading)
  .use(Search)
  .use(IndexBar)
  .use(IndexAnchor)
  .use(Icon)
  .use(Picker)
  .use(Popup)
  .use(Button);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
