import { Toast } from 'vant';

/**
 * 监听错误
 */
export const catchError = (error, msg) => {
  const { response } = error;
  if (response) {
    console.log(response);
    // const { message: message2 } = response.data;
    Toast.fail('失败文案');
  } else if (msg) {
    Toast.fail('失败文案');
  }
  throw error;
};


