import axios from 'axios';
import { baseURL } from './utils';

// 会员相关接口

/**
 * 查询会员
 * @param {*} memberNo 会员电话号码
 * @returns
 */
export const queryMemberByNo = memberNo => axios
  .get(`${baseURL}/member/${memberNo}`)
  .catch(error => {
    console.error('查询会员失败', error.response);
  });



/**
 * 查询详情
 * @param {*} id 会员id
 * @returns
 */
export const queryDetailById = id => axios
  .get(`${baseURL}/member/detail/${id}`)
  .catch(error => {
    console.error('查询会员失败', error.response);
  });


/**
 * 新增会员列表查询
 * @param {*} memberNo 会员电话号码
 * @returns
 */
export const querynewMember = () => axios
  .post(`${baseURL}/member/query`)
  .catch(error => {
    console.error('查询会员失败', error.response);
  });


/**
 * 新增会员列表查询
 * @param {*} memberNo 会员电话号码
 * @returns
 */
export const queryMember = (param) => axios
  .post(`${baseURL}/member/query`, {}, { params: param })
  .catch(error => {
    console.error('查询会员失败', error.response);
  });


/**
 * 新增会员
 * @param {*} param
 * @returns
 */
export const memberAdd = (param) => axios.post(`${baseURL}/member/add`, {}, { params: param }).catch(error => {
  console.error('新增会员失败', error.response);
});


