<template>
  <div class="app">
    <!-- 开启顶部安全区适配 -->
    <!-- <van-nav-bar safe-area-inset-top /> -->
    <router-view />
    <!-- 开启底部安全区适配 -->
    <van-number-keyboard safe-area-inset-bottom />
  </div>
</template>


<script>
import { projectAll, queryStaff, recordPaytypes } from '@/api/index';

export default {
  beforeCreate() {
    if (!localStorage.getItem('isLogin')) {
      this.$router.push({
        path: '/login'
      });
    } else {
      this.$store.commit('setUserInfo', JSON.parse(localStorage.getItem('userInfo')));
    }
  },
  created() {
    // `this` 指向 vm 实例
    console.log(`a is: ${this.a}`);
    projectAll().then(res => {
      if (res && res.data.code === 0) {
        this.$store.commit('setProject', res.data.data);
      }
    });
    queryStaff().then(res => {
      if (res && res.data.code === 0) {
        this.$store.commit('setStaff', res.data.data);
      }
    });

    recordPaytypes().then(res => {
      if (res && res.data.code === 0) {
        this.$store.commit('setPaytypes', res.data.data);
      }
    });
  }
};
</script>


